html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  background-color: #312f2f;
}

h1,
h2,
h3 {
  margin: 1rem;
  color: #a6d9f7;
  text-align: center;
  font-weight: bold;
}

h1 {
  font-size: 4rem;
  text-align: left;
}
@media (min-width: 700px) {
  h1 {
    text-align: center;
  }
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.8rem;
}

p {
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.form__input {
  color: #a6d9f7;
  background-color: #312f2f;
  width: 100%;
  height: 4rem;
  margin: 1rem 0;
  border: none;
  outline: 5px;
}
.form .btn {
  width: 100%;
  font-size: 2rem;
  color: #312f2f;
  background-color: #a6d9f7;
  border: transparent;
  border-radius: 5px;
  margin-bottom: 1.5rem;
  transition: 0.3s ease-in-out;
}
.form .btn:hover {
  background-color: #312f2f;
  border: 2px solid #a6d9f7;
  color: #a6d9f7;
}

.noTasks {
  background-color: #312f2f;
  margin-top: 2rem;
  border: 3px solid #a6d9f7;
  border-radius: 5px;
}

.tasks {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
  padding: 3rem;
  width: 100%;
  height: 4rem;
  background-color: #312f2f;
  color: #a6d9f7;
  border-radius: 5px;
}
.tasks .task {
  margin: 0;
  font-size: 2.5rem;
  overflow: hidden;
}
.tasks .icons {
  margin: 1rem;
}

.taskComplete {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
  padding: 3rem;
  width: 100%;
  height: 4rem;
  color: #312f2f;
  background-color: #a6d9f7;
  border-radius: 5px;
}
.taskComplete .task {
  margin: 0;
  font-size: 2.5rem;
  text-decoration: line-through;
  overflow: hidden;
}
.taskComplete .icons {
  margin: 1rem;
}